 //     
function imgShow(outerdiv, img, _this){  
    //console.log(_this);
    var src = _this.src;//pimgsrc    
    $(img).attr("src", src);//#bigimgsrc   
    //console.log("src"+src);
    $(img).on("load",function(){
        var h = $(this).height();
        var w = $(this).width();
        //console.log("height:"+h);
        var top= ($(window).height()-h)/2; 
        //console.log(top);
        if(top<0){
        	$(img).css("top",0).css("margin-left",-w/2);
        }else{        	
        	$(img).css("top",top).css("margin-left",-w/2);
        }
        $(img).click(function(e){//    
        	e.stopPropagation();
   			$(outerdiv).remove();
     	});
    });        
	$(outerdiv).click(function(){//    
   	   $(outerdiv).remove()
	});
	
} 
export { //
  imgShow
}  
